<template>
	<div>
		<header>
			<div class="container">
				<div class="row">
					<div class="col col-sm-9">
						<h1>Site</h1>
					</div>
					<div class="col col-sm-3">
						<div class="alert alert-default">
							<div>In Cart: {{ $store.getters['cart/length'] }}</div>
							<div>Total: {{ $store.getters['cart/total'] }}</div>
						</div>
					</div>
				</div>
				<hr>
			</div>
		</header>
		<section>
			<div class="wrapper">
				<div class="flex-row">
					<div class="flex-col-menu">
						<ul class="menu-list">
							<li :class="activeClass(item.route)" v-for="item in menu" :key="item.route"
              @click="makeActive(item.route)">
								<router-link :to="{ name: item.route }" class="router">
									{{ item.text }}
								</router-link>
							</li>
						</ul>
					</div>
          <div class="flex-col-prod">
            <div>
              <router-view />
            </div>
          </div>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
	export default {
		data: () => ({
			menu: [
				{ route: 'catalog', text: 'Products' },
				{ route: 'cart', text: 'Cart' },
				{ route: 'checkout', text: 'Order' }
			],
        active: 'catalog'
		}),
    methods: {
      makeActive: function(item){
        // When a model is changed, the view will be automatically updated.
        this.active = item;
      },
      activeClass(liClass){
        if(liClass === this.active){
          return this.active = liClass;
        }
      }
    }
		// $route, $router
	}
</script>

<style lang="less">
  @border: 1px;
  @columns: 12;
  @offset: 30px;
  @offset_one_side: (@offset / 2);

  .wrapper{
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 30px;
  }


  .flex-row{
    display: flex;
    flex-wrap: nowrap;
    margin: 0 (-@offset_one_side);
  }

  .flex-col-menu{
    width: (20% - @offset_one_side);
    box-sizing: border-box;
  }

  .flex-col-prod{
    width: (80% - @offset_one_side);
    box-sizing: border-box;
  }

  .size(@n){
    width: calc(100% / @columns * @n - @offset);
  }

  .menu-list{
    width: 20%;
    min-width: 184px;
  }

  li{
    background-color:#5597b4;
    list-style-type: none;
    font-weight:bold;
    font-size:16px;
    text-decoration:none;
    line-height:1;
    text-transform: uppercase;


    -webkit-transition:background-color 0.25s;
    -moz-transition:background-color 0.25s;
    transition:background-color 0.25s;
  }

  .router{
    color:#fff;
    text-decoration:none;
    padding: 18px 30px;
    display: block;
  }
  .router:hover{
    text-decoration:none;
    color: #fff
  }

  .checkout, .cart, .catalog{
    background-color:#e35885;
  }

</style>